@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #aaa6a6;
}


.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  padding: 1rem;
  background-color: white;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
}
.formElements {
    display: flex;
    flex-direction: column;
    font-weight: bold;
}

.formElement {
    display: flex;
    flex-direction: column;
}

.formElement input {
    margin-top: 8px;
    margin-bottom: 8px;
    font-size: 1em;
    border: 2px solid #ccc;
    padding: 0.5em;
}

.formElement input:focus {
    outline: none;
    border: 2px solid #4f005f;
}

.formElement label {
    font-size: larger;
}

.formButton {
    font: inherit;
    cursor: pointer;
    padding: 0 15px;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: white;
    margin-right: 1rem;
}
.userEntry {
    border: 1px solid #ccc;
    padding: 8px;
}
.usersContainer {
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.title {
  font-size: 20px;
  background-color: #4f005f;
  border-radius: 12px 12px 0 0;
  color: white;
  font-weight: bold;
  padding: 15px;
}

.overlay {
  background-color: black;
  position: absolute;
  z-index: 100;
  height: 100%;
  width: 100%;
  opacity: 70%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.body {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  background-color: white;
  margin: 25% auto;
  width: 50rem;
  max-width: 95%;
  position: relative;
  z-index: 101;
}

.errorMessage {
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.buttonContainer {
  display: flex;
  justify-content: flex-end;
}

.modalButton {
  margin: 12px;
  font: inherit;
  cursor: pointer;
  padding: 2px 15px;
  border: 1px solid #40005d;
  background-color: #40005d;
  color: white;
}

.modalContainer {
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
}
